<template>
  <div id="app">    
    <div id="quadro" :style="`background: url(${ corQuadro })`">
      <div id="poster" :style="`background: ${ corPoster }`">
        <vue-draggable-resizable v-if="mostrarTexto" :w="width" :h="height" @dragging="onDrag" @resizing="onResize" :parent="true">
          <textarea name="text" id="txt" :cols="width/12" :rows="height/24"></textarea>
        </vue-draggable-resizable> 
      </div>      
    </div>
    <div id="coresQuadro">
      <h2>Escolha seu quadro: </h2>
      <div>
        <input type="radio" id="branco" :value="corQuadroBranco" v-model="corQuadro">
        <label for="branco">Quadro Branco</label>
      </div>
      <div>
        <input type="radio" id="preto" :value="corQuadroPreto" v-model="corQuadro">
        <label for="preto">Quadro Preto</label>
      </div>
    </div>
    <div id="coresPoster">
      <h2>Escolha seu poster: </h2>
      <div>
        <input type="radio" id="vermelho" value="#f00" v-model="corPoster">
        <label for="vermelho">Poster Vermelho</label>
      </div>
      <div>
        <input type="radio" id="verde" value="#0f0" v-model="corPoster">
        <label for="verde">Poster Verde</label>
      </div>
      <div>
        <input type="radio" id="azul" value="#00f" v-model="corPoster">
        <label for="azul">Poster Azul</label>
      </div>
    </div>
    <div id="quadroPronto">
      <h2>Quadros prontos para você: </h2>
      <div>
        <input type="radio" id="dale" :value="posterDale" v-model="corPoster" >
        <label for="dalse">Quadro #Dale10</label>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data: function () {
      return {
        width: 125,
        height: 50,
        corPoster: '#fff',
        corQuadroPreto: require("./assets/casquinhaQuadroPreto.png"),
        corQuadroBranco: require("./assets/casquinhaQuadroBranco.png"),
        corQuadro: '',
        mostrarTexto: true,
        posterDale: `url('${require("./assets/quadroDalessandro.jpg")}`
      }
    },
    methods: {
      onResize: function (x, y, width, height) {
        this.x = x
        this.y = y
        this.width = width
        this.height = height
      },
      onDrag: function (x, y) {
        this.x = x
        this.y = y
      },
      quadroDalessandro: function() {
        this.corPoster = this.posterDale
        this.corQuadro = this.corQuadroBranco  
        this.mostrarTexto = false      
      }
    },
    created() {
      this.corQuadro = this.corQuadroPreto
    },
    watch: {
      corPoster: function(poster) {
        if(poster === this.posterDale) this.quadroDalessandro()
        else this.mostrarTexto = true
      }
    }
  }
</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin: 60px;
    display: inline-block;
  }

  #quadro {
    position: relative;
    display: inline-block;
    height: 450px; 
    width: 450px;
    z-index: 1;
  }

  #poster {
    z-index: 2;
    position: absolute;
    height: 330px; 
    width: 230px;
    margin-top: 60px;
    margin-left: 112px;
    overflow: hidden;
  }

  #coresQuadro {
    display: inline-block;
    margin-right: 25px;
  }

  #coresPoster {
    display: inline-block;
    margin-right: 25px;
  }

  #quadroPronto {
    display: inline-block;
  }

  h2 {
    font-size: 1rem;
  }

  textarea {
    background: transparent;
    border: none;
    outline: none;
    font-size: 1.5rem;
    font-weight: 40;
    resize: none;
    z-index: 3;
  }
</style>
